import ReactDOM from 'react-dom'
import React, { useState, useContext, createContext, useEffect } from 'react'
import Button from '@mui/material/Button'
import Dialog from '@mui/material/Dialog'
import DialogTitle from '@mui/material/DialogTitle'
import DialogContent from '@mui/material/DialogContent'
import DialogActions from '@mui/material/DialogActions'
import IconButton from '@mui/material/IconButton'
import CircularProgress from '@mui/material/CircularProgress'
import Box from '@mui/material/Box'
import CloseIcon from '@mui/icons-material/Close'
import { log } from '../_utility'
import pMap, { pMapSkip } from 'p-map'
import {
  getAppFields,
  getAppInfo,
  getAppRecord,
  createAppRecord,
  updateAppRecord,
} from '../_kintone'

const getKey = () => {
  return Math.random()
}

const LookupListContext = createContext({})
const LookupListProvider = (props) => {
  const [lookupList, setLookupList] = useState([])
  return (
    <LookupListContext.Provider value={{ lookupList, setLookupList }}>
      {props.children}
    </LookupListContext.Provider>
  )
}

const AlertContext = createContext({})
const AlertProvider = (props) => {
  const [isOpen, setIsOpen] = useState(false)
  const [title, setTitle] = useState('')
  const [text, setText] = useState('')
  return (
    <AlertContext.Provider
      value={{
        showAlert: (title, text) => {
          setTitle(title)
          setText(text)
          setIsOpen(true)
        },
      }}
    >
      {props.children}

      <Dialog
        open={isOpen}
        onClose={() => {
          setIsOpen(false)
        }}
      >
        <DialogTitle>{title}</DialogTitle>
        <DialogContent>{text}</DialogContent>
        <DialogActions>
          <Button
            onClick={() => {
              setIsOpen(false)
            }}
          >
            OK
          </Button>
        </DialogActions>
      </Dialog>
    </AlertContext.Provider>
  )
}

const LoadingContext = createContext({})
const LoadingProvider = (props) => {
  const [isOpen, setIsOpen] = useState(false)
  const [text, setText] = useState('')
  return (
    <LoadingContext.Provider
      value={{
        showLoading: (text) => {
          setText(text)
          setIsOpen(true)
        },
        closeLoading: () => {
          setIsOpen(false)
        },
      }}
    >
      {props.children}

      <Dialog
        open={isOpen}
        PaperProps={{
          style: {
            backgroundColor: 'transparent',
            boxShadow: 'none',
          },
        }}
      >
        <DialogContent>
          <Box sx={{ textAlign: 'center' }}>
            <CircularProgress />
            <p style={{ color: 'white' }}>{text}</p>
          </Box>
        </DialogContent>
      </Dialog>
    </LoadingContext.Provider>
  )
}

const AppDialogContent = () => {
  const { lookupList } = useContext(LookupListContext)
  return (
    <div>
      <h2></h2>
      {lookupList.targets.map((item) => (
        <div key={getKey()}>
          <h3>
            [{item.recordExist ? '' : ''}] {item.name}{' '}
            {item.updated && ''}
          </h3>
          <ul>
            {item.fields.map((field) => (
              <li
                key={getKey()}
                style={{
                  fontWeight:
                    field.beforeValue !== field.afterValue ? 'bold' : 'normal',
                }}
              >
                {field.label}
                <br />
                <span style={{ marginLeft: '16px' }}>
                  {field.beforeValue || '[]'} {' '}
                  {field.afterValue || '[]'}
                </span>
              </li>
            ))}
          </ul>
        </div>
      ))}
    </div>
  )
}

const AppDialog = ({ isOpen, close }) => {
  const { setLookupList, lookupList } = useContext(LookupListContext)
  const { showAlert } = useContext(AlertContext)
  const [progress, setProgress] = useState(false)

  return (
    <Dialog
      open={isOpen}
      onClose={close}
      PaperProps={{
        style: {
          width: '50vw',
          maxWidth: 'none',
        },
      }}
    >
      <DialogTitle>
        
        <IconButton
          aria-label="close"
          onClick={close}
          sx={{
            position: 'absolute',
            right: 8,
            top: 8,
            color: (theme) => theme.palette.grey[500],
          }}
        >
          <CloseIcon />
        </IconButton>
      </DialogTitle>
      <DialogContent>
        <AppDialogContent />
      </DialogContent>
      <DialogActions>
        <Button
          disabled={progress}
          onClick={async () => {
            setProgress(true)

            await updateLookupList(lookupList, (index) => {
              lookupList.targets[index].updated = true
              setLookupList({ ...lookupList })
            })

            setProgress(false)
            close()
            showAlert('', '')
          }}
        >
          {progress && (
            <CircularProgress size={18} sx={{ marginRight: '8px' }} />
          )}
          {!progress ? '' : ''}
        </Button>
      </DialogActions>
    </Dialog>
  )
}

const Main = () => {
  const { setLookupList } = useContext(LookupListContext)
  const { showLoading, closeLoading } = useContext(LoadingContext)
  const [isOpen, setIsOpen] = useState(false)

  const close = () => {
    setIsOpen(false)
  }

  // 
  useEffect(() => {
    // 
    kintone.events.on(
      ['app.record.create.submit.success', 'app.record.edit.submit.success'],
      async (event) => {
        const { record, appId } = event

        const appIds = window.kintoneEnv.autoLookupAppIds
        if (appIds.indexOf(appId) === -1) return event

        showLoading('')

        const lookupList = await getLookupList(appId, record.HER_SYS_ID.value)
        log('lookupList', lookupList)

        closeLoading()

        if (!lookupList || !lookupList.targets.length) return

        setLookupList(lookupList)
        setIsOpen(true)

        return event
      },
    )
  }, [])

  return (
    <div>
      {/* <Button variant="contained" onClick={open}>
        Dialog
      </Button> */}
      <AppDialog isOpen={isOpen} close={close} />
    </div>
  )
}

const App = () => {
  return (
    <LookupListProvider>
      <AlertProvider>
        <LoadingProvider>
          <Main />
        </LoadingProvider>
      </AlertProvider>
    </LookupListProvider>
  )
}

export default async () => {
  log('auto-lookup')
  const app = document.createElement('div')
  app.setAttribute('id', 'auto-lookup')
  document.body.appendChild(app)
  ReactDOM.render(<App />, app)
  // log(await getLookupList(18))
}

const getLookupList = async (myId, herSysId) => {
  try {
    const appIds = window.kintoneEnv.autoLookupAppIds
    const myFields = await getAppFields(myId)
    const myRecord = await getAppRecord(myId, `HER_SYS_ID=${herSysId}`)
    log(myFields, myRecord)

    const targets = await pMap(appIds, async (appId) => {
      if (appId === myId) return pMapSkip
      const info = await getAppInfo(appId)
      const record = await getAppRecord(appId, `HER_SYS_ID=${herSysId}`)
      const fields = await getAppFields(appId)
      const matchedFields = []
      Object.entries(fields).forEach(([key, value]) => {
        if (
          [
            '',
            '',
            '',
            '',
            '',
            'HER_SYS_ID',
          ].indexOf(key) > -1
        )
          return
        if (!myFields.hasOwnProperty(key) || !myRecord.hasOwnProperty(key))
          return
        value.beforeValue = record ? record[key].value : ''
        value.afterValue = myRecord[key].value

        // if (value.beforeValue === value.afterValue) return // 
        matchedFields.push(value)
      })

      if (!matchedFields.length) return pMapSkip // 

      return {
        name: info.name,
        appId,
        fields: matchedFields,
        recordExist: !!record,
      }
    })

    return {
      herSysId,
      targets,
    }
  } catch (err) {
    console.error(err)
    alert(err.message)
    return null
  }
}

const updateLookupList = async (lookupList, onUpdate) => {
  await pMap(lookupList.targets, async (item, index) => {
    try {
      if (!item.recordExist) {
        await createAppRecord(item.appId, {
          HER_SYS_ID: { value: lookupList.herSysId },
        })
      }

      const fields = {}
      item.fields.forEach((field) => {
        fields[field.code] = { value: field.afterValue }
      })
      log('update fields data', fields)

      await updateAppRecord(
        item.appId,
        { field: 'HER_SYS_ID', value: lookupList.herSysId },
        fields,
      )
    } catch (err) {
      console.error(item, err)
      // alert(err.message)
    }

    onUpdate(index)
  })
}
